import React from 'react';
import QueueAnim from 'rc-queue-anim';
import Header from './components/Header';
import InputBar from './components/InputBar';
import Todo from './components/Todo';
import './App.sass';

class App extends React.Component {
  state = {
    todoData: [],
  };

  onTodoInputEnter = value => {
    this.addTodo(value);
  };

  addTodo = text => {
    const { todoData } = this.state;
    todoData.push({ id: new Date().getTime(), text, completed: false });
    this.setState({ todoData });
  };

  onTodoChange = ({ id, text, completed }) => {
    let { todoData } = this.state;
    todoData = todoData.map(todo => {
      if (todo.id === id) {
        return { id, text, completed };
      }
      return todo;
    });
    this.setState({ todoData });
  };

  onTodoRemove = id => {
    let { todoData } = this.state;
    todoData = todoData.filter(todo => todo.id !== id);
    this.setState({ todoData });
  };

  render() {
    const { todoData } = this.state;

    return (
      <div className="app">
        <Header title="Todo App üìù" subTitle="‚öõÔ∏è a react app ‚öõÔ∏è" />
        <div className="container">
          <section className="section">
            <InputBar onEnter={this.onTodoInputEnter} />
          </section>

          <section className="section todo-list">
            <QueueAnim delay={200}>
              {todoData.map(todo => (
                <Todo
                  key={todo.id}
                  id={todo.id}
                  text={todo.text}
                  completed={todo.completed}
                  onChange={this.onTodoChange}
                  onRemove={() => this.onTodoRemove(todo.id)}
                />
              ))}
            </QueueAnim>
          </section>
        </div>
      </div>
    );
  }
}

export default App;
