import React from 'react';
import PropTypes from 'prop-types';

import './index.sass';

class Todo extends React.PureComponent {
  state = {
    editing: false,
  };

  render() {
    const { id, text, completed } = this.props;
    const { editing } = this.state;

    return (
      <div className="box">
        <div className="level is-mobile">
          <div className="level-left todo-left">
            <a
              className={`subtitle is-5 todo-check-btn${
                completed ? ' checked' : ''
              }`}
              onClick={() =>
                this.props.onChange({ id, text, completed: !completed })
              }
            >
              ‚úÖ
            </a>
            <div className="subtitle is-4 todo-text">
              {completed ? <del>{text}</del> : text}
            </div>
          </div>
          <div className="level-right todo-right">
            {/* <a className="icon" onClick={() => this.setState({ editing: true })}>üìù</a> */}
            <a className="icon" onClick={this.props.onRemove}>
              ‚ùå
            </a>
          </div>
        </div>
      </div>
    );
  }
}

Todo.propTypes = {
  id: PropTypes.any.isRequired,
  text: PropTypes.string.isRequired,
  completed: PropTypes.bool,
  onChange: PropTypes.func,
  onRemove: PropTypes.func,
};

Todo.defaultProps = {
  completed: false,
  onChange: () => {},
  onRemove: () => {},
};

export default Todo;
